@use "../variables/breakpoints" as *;

// Breakpoints

$breakpoints-up: ();

$breakpoints-down: (
  "max-medium": $max-medium,
  "medium": $medium,
  "small": $small,
  "xsmall": $x-small,
);

@mixin breakpoint-up($size) {
  @media (min-width: map-get($breakpoints-up, $size)) {
    @content;
  }
}

@mixin breakpoint-down($size) {
  @media (max-width: map-get($breakpoints-down, $size)) {
    @content;
  }
}
